header {
    height: 70px;
    width: 100%;
    display: flex;
    justify-content: space-between;
    margin-bottom: 10px;
}

.logo-container {
    width: 70px;
    padding: 25px;
}

.options-container {
    width: 50%;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: flex-end;
}

.option {
    padding: 10px 15px;
    cursor: pointer;
}