.sign-in {
    width: 380px;
    display: flex;
    flex-direction: column;
    border: 2px solid black;
    padding: 10px 20px;
    box-shadow: 2px 2px 2px grey;
}

.sign-in .title {
    margin: 20px 0;
}

.buttons {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
}